<template>
  <div id="app">
    <question
      v-if="showQuestion"
      @update-words="updateWords"
      @hide="hideQuestion"
    />
    <answer
      v-for="(word, index) in words"
      :key="index"
      :word="word"
    />
  </div>
</template>

<script>
import Answer from './components/Answer.vue'
import Question from './components/Question.vue'

export default {
  name: 'app',
  components: {
    Answer,
    Question,
  },
  data() {
    return {
      words: [],
      showQuestion: true,
    }
  },
  methods: {
    updateWords(words) {
      this.words = words;
    },
    hideQuestion() {
      this.showQuestion = false;
    }
  },
};
</script>

<style>
body {
  background-color: #d3d3d3;
}
#app {
}
</style>
