<template>
    <div
        :class="{answer: 'answer', correct, incorrect}"
    >
        <button
            class="play"
            @click="readWord"
        >▶️</button>
        <input
            v-model="answer"
            :disabled="correct"
            class="answer-input"
            type="text"
            role="textbox"
            autocapitalize="off"
            autocomplete="off"
            autocorrect="off"
            spellcheck="false"
            @change="onChange"
        />
        <button
            class="submit"
            @click="submit"
        >OK</button>
    </div>
</template>


<script>
export default {
    name: 'Answer',
    props: {
        word: {
            type: String,
            default: null,
        },
    },
    data() {
        return {
            answer: '',
            synth: window.speechSynthesis,
            submitted: false,
        };
    },
    computed: {
        correct() {
            return this.submitted && this.answer === this.word;
        },
        incorrect() {
            return this.submitted && this.answer !== this.word;
        },
    },
    methods: {
        readWord() {
            const voice = this.synth.getVoices().find((voice) => voice.lang == 'fr-FR');
            var utterThis = new SpeechSynthesisUtterance(this.word);
            utterThis.voice = voice;
            this.synth.speak(utterThis);
        },
        submit() {
            this.submitted = true;
        },
        onChange() {
            this.submitted = false;
        }
    },
}
</script>

<style lang="scss">
.answer {
    height: 4em;
    display: flex;
    flex-direction: row;
    background: #e5e5e5;
    align-items: stretch;
    margin: 1em auto;
    width: 40em;
    max-width: 100%;

    button,
    input {
        margin: 0.5rem;
    }

    button {
        width: 3em;
    }

    .answer-input {
        font-size: 2em;
        flex-grow: 1;
        padding: 0.25rem;
    }

    &.correct::after {
        content: '✔';
        color: green;
    }

    &.incorrect::after {
        content: '❌';
        color: red;
    }
}
</style>